import React from 'react'
import './Footer.css'
import { Link } from 'react-router-dom'

function Footer() {
  return (
    <div className='footer-container'>
      <section class='social-media'>
        <div class='social-media-wrap'>
          <div class='footer-logo'>
            <Link to='/' className='social-logo'>
              Stream
              <i class='fab fa-typo3' />
            </Link>
          </div>
          <small class='website-rights'>TaShU Â© 2021</small>
          <div class='social-icons'>
            <Link
              class='social-icon-link facebook'
              to={{
                pathname: 'https://www.facebook.com/ankur.das.560/',
              }}
              target='_blank'
              aria-label='Facebook'
            >
              <i class='fab fa-facebook-f' />
            </Link>
            <Link
              class='social-icon-link instagram'
              to={{
                pathname: 'https://www.instagram.com/tashu_das/',
              }}
              target='_blank'
              aria-label='Instagram'
            >
              <i class='fab fa-instagram' />
            </Link>
            <Link
              class='social-icon-link youtube'
              to={{
                pathname:
                  'https://www.youtube.com/channel/UCZUP9s6Z0gGadFSfM3nvARg',
              }}
              target='_blank'
              aria-label='Youtube'
            >
              <i class='fab fa-youtube' />
            </Link>
            <Link
              class='social-icon-link twitter'
              to={{
                pathname: 'https://twitter.com/TaShUOP',
              }}
              target='_blank'
              aria-label='Twitter'
            >
              <i class='fab fa-twitter' />
            </Link>
            <Link
              class='social-icon-link twitter'
              to={{
                pathname: 'https://www.linkedin.com/in/tashuop/',
              }}
              target='_blank'
              aria-label='LinkedIn'
            >
              <i class='fab fa-linkedin' />
            </Link>
          </div>
        </div>
      </section>
    </div>
  )
}

export default Footer
